"use client";

import React, { useEffect, useState } from 'react';

interface Subscription {
  subscription: string;
  expiry: string;
}

interface UserInfo {
  username: string;
  subscriptions: Subscription[];
  ip: string;
  hwid: string;
  createdate: string;
  lastlogin: string;
}

